"use client";

import React from "react";
import {
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
  Box,
} from "@chakra-ui/react";

const faqItems = [
  {
    question: "AzPayGo nədir?",
    answer:
      "AzPayGo mobil tətbiqi ödənişlərinizi asan və təhlükəsiz şəkildə etməyiniz üçün hazırlanmışdır.",
  },
  {
    question: "Necə qeydiyyatdan keçə bilərəm?",
    answer:
      "Tətbiqi yükləyin, mobil nömrənizi daxil edin və SMS vasitəsilə təsdiqləyin.",
  },
  {
    question: "Ödənişlərim təhlükəsizdirmi?",
    answer:
      "Bəli, bütün ödənişlər beynəlxalq təhlükəsizlik standartlarına uyğun qorunur.",
  },
  {
    question: "Hansı xidmətlərdən istifadə edə bilərəm?",
    answer:
      "Kommunal, mobil, internet, TV və digər xidmətlər üçün ödəniş edə bilərsiniz.",
  },
];

export default function BasicDemo() {
  return (
    <Accordion
      allowMultiple
      w="full"
      maxW="3xl"
      rounded="xl"
      shadow="md"
      bg="white"
    >
      {faqItems.map((item, idx) => (
        <AccordionItem key={idx} border="none">
          <h2>
            <AccordionButton
              _expanded={{ bg: "blue.50", color: "blue.600" }}
              px={4}
              py={3}
              borderBottom="1px solid"
              borderColor="gray.200"
            >
              <Box as="span" flex="1" textAlign="left" fontWeight="semibold">
                {item.question}
              </Box>
              <AccordionIcon />
            </AccordionButton>
          </h2>
          <AccordionPanel pb={4} textAlign="left" color="gray.700">
            {item.answer}
          </AccordionPanel>
        </AccordionItem>
      ))}
    </Accordion>
  );
}
